<template>
  <div>
    <small-card-form
      title="Démarrer un vote"
      buttonSubmitText="Créer un vote"
      :submitMethod="createVote"
    >
      <div class="form-group" slot="input">
        <label for="newVote-title">Titre</label>
        <input
          type="text"
          class="form-control"
          id="newVote-title"
          v-model="voteTitle"
          placeholder="Titre de votre vote"
          name="newVote-title"
          maxlength="30"
          pattern="([^'\x22]{1,})+"
          title="Pas d'apostrophes autorisées"
          required
        />
      </div>
      <div class="form-group" slot="input">
        <label for="newVote-question">Question</label>
        <textarea
          type="text"
          id="newVote-question"
          class="form-control"
          v-model="voteQuestion"
          placeholder="Saisissez une question non ambigüe"
          name="newVote-question"
          pattern="([^'\x22])++"
          title="Pas d'apostrophes autorisées"
          required
        ></textarea>
      </div>
    </small-card-form>
    <!-- votes en cours -->
    <PreviewCardsList :list="inprogressVotes" titleList="En cours" />
    <!-- votes fermés -->
    <PreviewCardsList :list="closedVotes" titleList="Fermés" />
    <!-- votes archivés -->
    <PreviewCardsList :list="archivedVotes" titleList="Archivés" />
  </div>
</template>

<script>
import SmallCardForm from "@/components/SmallCardForm";
import PreviewCardsList from "@/components/PreviewCardsList";
import ApiCalls from "@/mixins/api-calls"

export default {
  name: "Account",
  components: {
    SmallCardForm,
    PreviewCardsList
  },
  mixins: [ApiCalls],
  data: function() {
    return {
      API_URL: process.env.VUE_APP_API_URL,
      token: localStorage.getItem('user-token'),
      currentUser: JSON.parse(localStorage.getItem('currentUser')),
      voteTitle: null,
      voteQuestion: null,
      inprogressVotes: [],
      closedVotes: [],
      archivedVotes: []
    };
  },
  created() {
    this.getInProgressVotes();
    this.getClosedVote();
  },
  methods: {
    getInProgressVotes() {
      this.findRecord('votes', {
          filter: {
            user_id: this.currentUser._id,
            status: {
              $in: [0, 1]
            }
          },
          sort: { _modified: -1 }
        })
        .then(response => this.inprogressVotes = response.data.entries)
        .catch(e => console.error(e))
    },
    getClosedVote() {
      this.findRecord('votes', {
          filter: {
            user_id: this.currentUser._id,
            status: 2
          },
          sort: { _modified: -1 }
        })
        .then(response => this.closedVotes = response.data.entries)
        .catch(e => console.error(e));
    },
    createVote() {
      this.createOrSaveRecord('votes', {
        title: this.voteTitle,
        description: this.voteQuestion,
        user_id: this.currentUser._id,
        creationDate: new Date(),
        status: 0,
        nbBulletins: 0,
        nbChoices: 0
      })
      .then(response => this.$router.push("/edit/" + response.data._id))
      .catch(err => console.error(err))
    },
  }
};
</script>

<style lang="scss"></style>
