<template>
  <div class="card col-12 my-3 mx-0">
    <div class="card-body">
      <div class="row mx-0 mb-2">
        <h4 class="card-title">{{ item.name }}</h4>
        <h5 class="card-subtitle mention ml-auto mt-1">{{ mention }}</h5>
      </div>
      <div v-if="voteStatus == '1'" class="d-flex w-100 justify-content-between">
        <!-- <i class="fa fa-frown-o"></i> -->
        <div v-for="(v, k) in mentions" :key="item._id + '_' + v">
          <input
            type="radio"
            :name="item._id + '_' + v"
            :value="k"
            v-model="pickedMention"
            :disabled="hasAlreadyVoted"
            @click="hasAlreadyVoted ? undefined : updateBulletin(k)"
          />
          <label>{{ k }}</label>
        </div>
        <!-- <i class="fa fa-smile-o"></i> -->
      </div>
      <div v-else class="nmb_voices_bar">
        <div
          v-for="(k, v) in mentions"
          :style="{ width: getWidth(v) + '%' }"
          :key="item._id + '_' + k"
        ></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Proposition",
  props: {
    item: {
      type: Object,
      required: true
    },
    voteStatus: String,
    hasAlreadyVoted: Boolean,
    childToParent: Function,
    choice_value: String
  },
  data: function() {
    return {
      mentions: {
        nb1: "Non",
        nb2: "Insuffisant",
        nb3: "Passable",
        nb4: "Neutre",
        nb5: "Assez bien",
        nb6: "Bien"
      },
      pickedMention: this.hasAlreadyVoted ? this.choice_value : "nb6"
    };
  },
  computed: {
    mention: function() {
      return this.mentions[this.pickedMention];
    }
  },
  methods: {
    updateBulletin(value) {
      this.$emit("childToParent", this.item._id, value);
    },
    getWidth(k) {
      let total = 0;
      for (let i = 1; i < 6; i++) {
        total += this.item[k];
      }
      const width = (this.item[k] * 100) / total;
      return width.toString();
    }
  }
};
</script>

<style lang="scss" scoped></style>
