<script>
import axios from "axios";

export default {
  name: "ApiCalls",
  data: function() {
    return {
      API_URL: process.env.VUE_APP_API_URL,
      API_TOKEN: process.env.VUE_APP_API_TOKEN,
      user_token: localStorage.getItem("user-token") || '',
      user: localStorage.getItem("currentUser") || {}
    };
  },
  methods: {
    async login(user) {
      return new Promise((resolve, reject) => {
        axios.defaults.baseURL = this.API_URL;
        axios
          .post(`cockpit/authUser?token=${this.API_TOKEN}`, user)
          .then(response => {
            localStorage.setItem("user-token", response.data.api_key);
            localStorage.setItem("currentUser", JSON.stringify(response.data));
            resolve(response);
          })
          .catch(err => {
            reject(err);
          });
      })
    },
    async logout() {
      localStorage.removeItem('currentUser');
      localStorage.removeItem('user-token');
      this.$router.push("/");
    },
    async saveUser(datas) {
      return new Promise((resolve, reject) => {
        axios.defaults.baseURL = this.API_URL;
        axios
          .post(`cockpit/saveUser?token=${this.API_TOKEN}`, {
            user: datas
          })
          .then(response => {
            const data = JSON.parse(response.config.data);
            resolve({user: data.user.user, password: data.user.password});
          })
          .catch(err => {
            reject(err);
          });
      })
      
    },
    async findRecord(model, data) {
      return new Promise((resolve, reject) => {
        axios.defaults.baseURL = this.API_URL;
        axios.defaults.headers.common["Cockpit-Token"] = this.user_token;
        axios
          .post(`/collections/get/${model}`, data)
          .then(response => resolve(response))
          .catch(e => reject(e));
      });
    },
    async createOrSaveRecord(model, data) {
      return new Promise((resolve, reject) => {
        axios.defaults.baseURL = this.API_URL;
        axios.defaults.headers.common["Cockpit-Token"] = this.user_token;
        // création du bulletin
        axios
          .post(`/collections/save/${model}`, { data: data })
          .then(response => resolve(response))
          .catch(e => reject(e));
      });
    },
    async removeRecord(model, data) {
      return new Promise((resolve, reject) => {
        axios.post(`/collections/remove/${model}`, {
          filter: data
        })
        .then(response => resolve(response))
        .catch(err => reject(err));
      });
    }
  }
};
</script>