<template>
  <div id="content" class="row main-container">
    <div id="description" class="col-lg-4 text-center">
      <img src="@/assets/img/logo.png" alt width="175px;" class="my-5" />
      <h2 class="mb-4">Le vote majoritaire en toute simplicité</h2>
      <p class="px-3">
        Avec Voton, organisez un vote en quelques clics,
        <br />sur la base du
        <b>scrutin Majoritaire</b>, un système de scrutin
        nouvelle génération démocratiquement et mathématiquement plus avancé que
        le scrutin Uninominatif à 2 tours classiques.
      </p>

      <button class="btn btn-primary" @click="createAnonymVote">Créer un vote</button>
    </div>

    <div class="d-flex col-lg-8 cards-ctn">
      <!------- LOGIN BLOC --------->
      <small-card-form
        title="Connexion"
        subTitle="Accéder à mon compte"
        buttonSubmitText="Me connecter"
        :error="errorAuthentication"
        :submitMethod="authentication"
      >
        <div class="form-group mb-3" slot="input">
          <label for="loginUsername">Nom d'utilisateur</label>
          <input
            type="text"
            class="form-control"
            id="loginUsername"
            v-model="loginUsername"
            placeholder="Nom d'utilisateur"
            name="loginUsername"
            required
          />
        </div>
        <div class="form-group mb-3" slot="input">
          <label for="loginPassword">Mot de passe (5 caractères minimum)</label>
          <input
            type="password"
            id="loginPassword"
            v-model="loginPassword"
            class="form-control"
            placeholder="Mot de passe"
            name="loginPassword"
            pattern="[^'\x22\s]{5,}"
            title="Espaces et apostrophes non autorisés"
            required
          />
        </div>
        <a
          class="d-block mt-2"
          href
          :onclick="reset_password()"
          slot="link"
        >Identifiant ou mot de passe oublié ?</a>
      </small-card-form>

      <!-- SIGN IN BLOC -->
      <small-card-form
        title="Inscription"
        subTitle="Créez un compte pour pouvoir sauvegarder vos votes"
        buttonSubmitText="M'inscrire"
        :error="errorSignin"
        :submitMethod="createUser"
      >
        <div class="form-group mb-3" slot="input">
          <label for="create-username">Nom d'utilisateur</label>
          <input
            type="text"
            class="form-control"
            id="create-username"
            v-model="createUsername"
            placeholder="Choisir un nom d'utilisateur"
            name="create-username"
            pattern="[A-Za-z0-9_\-]{1,}"
            title="Pas de caractères spéciaux"
            required
          />
        </div>
        <div class="form-group mb-3" slot="input">
          <label for="create-email">Email</label>
          <input
            type="email"
            id="create-email"
            v-model="createEmail"
            class="form-control"
            placeholder="Entrez un email valide"
            name="create-email"
            pattern="[^'\x22\s]{5,}"
            title="Espaces et apostrophes non autorisés"
            required
          />
        </div>
        <div class="form-group mb-3" slot="input">
          <label for="create-password">Mot de passe (5 caractères minimum)</label>
          <input
            type="password"
            id="create-password"
            class="form-control"
            v-model="createPassword"
            placeholder="Mot de passe"
            name="create-password"
            pattern="[^'\x22\s]+"
            title="Espaces et apostrophes non autorisés"
            min="6"
            required
          />
        </div>
        <div class="form-group mb-3" slot="input">
          <label for="create-password2">Confirmation du mot de passe</label>
          <input
            type="password"
            id="create-password2"
            class="form-control"
            v-bind:class="{ 'border-danger': pswdNotEqual }"
            v-model="confirmPassword"
            placeholder="Entrez une nouvelle fois le mot de passe"
            name="create-password2"
            pattern="[^'\x22\s]{5,}"
            title="Espaces et apostrophes non autorisés"
            min="6"
            required
          />
        </div>
      </small-card-form>

      <!------- INVITATION BLOC --------->
      <small-card-form
        title="Répondre à une invitation"
        subTitle="Rejoindre un vote en cours via un lien d'invitation"
        buttonSubmitText="Rejoindre"
        :submitMethod="joinVote"
      >
        <div class="form-group mb-3" slot="input">
          <label for="link">Lien d'invitation</label>
          <input
            type="url"
            id="link_vote"
            v-model="voteLink"
            class="form-control"
            placeholder="https://voton.com/vote/numeroduvote..."
            name="link"
            required
          />
        </div>
      </small-card-form>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import SmallCardForm from "@/components/SmallCardForm.vue";
import ApiCalls from "@/mixins/api-calls.vue";
import { uuid } from 'vue-uuid';

export default {
  name: "Home",
  data: function() {
    return {
      createUsername: null,
      createEmail: null,
      createPassword: "",
      confirmPassword: "",
      loginUsername: null,
      loginPassword: null,
      errorAuthentication: null,
      errorSignin: null,
      voteLink: null
    };
  },
  mixins: [ApiCalls],
  components: {
    SmallCardForm
  },
  computed: {
    pswdNotEqual: function() {
      return this.createPassword !== this.confirmPassword;
    }
  },
  methods: {
    authentication: function() {
      const { loginUsername, loginPassword } = this;
      this.login({
        user: loginUsername,
        password: loginPassword
      }).then(response => {
        this.$router.push(`/account/${response.data._id}`).catch(err => {
          console.log(err);
        });
      }).catch(() => {
        this.errorAuthentication = "Mauvais pseudo ou mot de passe";
      });
    },
    createUser: function() {
      const datas = {
        name: this.createUsername,
        user: this.createUsername,
        email: this.createEmail,
        password: this.createPassword,
        group: "simpleUser",
        active: true,
        api_key: "account-" + uuid.v1().replace(/-/g,'')
      }
      this.saveUser(datas).then(user => {
        this.login(user).then(loggedUser => {
          this.$router.push(`/account/${loggedUser.data._id}`);
        });
      }).catch(error => {
        console.log(error);
        if (error && error.response && error.response.status == 412)
          this.errorSignin = "Non d'utilisateur déjà existant";
      });
    },
    joinVote: function() {
      const regex = /^http[s]{0,1}:\/\/[a-zA-Z\d.]+\//g;
      const link = this.voteLink.replace(regex, "");
      this.$router.push(link);
    },
    createAnonymVote() {
      this.$router.push("/vote/new");
    },
    reset_password() {

    }
  }
};
</script>

<style lang="scss" scoped>
.main-container {
  justify-content: space-between;
  height: 100%;
  margin-top: 128px;
  padding: 0 64px;
}

.cards-ctn {
  flex-wrap: wrap;
}
</style>
