<template>
  <div class="card card-lg">
    <div class="card-body">
      <h3 class="card-title">{{ title }}</h3>
      <p class="card-subtitle">{{ subTitle }}</p>
      <p class="text-danger">{{ error }}</p>
      <form @submit="submitCallback" method="post" autocomplete="on">
        <slot name="input"></slot>
        <div class="form-group">
          <button type="submit" class="btn btn-primary btn" value="submit">{{ buttonSubmitText }}</button>
          <slot name="link"></slot>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "smallCardForm",
  props: {
    title: {
      type: String,
      required: true
    },
    subTitle: String,
    buttonSubmitText: {
      type: String,
      required: true
    },
    error: String,
    submitMethod: {
      type: Function,
      required: true
    }
  },
  methods: {
    submitCallback(e) {
      e.preventDefault();
      this.submitMethod();
    }
  }
};
</script>

<style lang="scss" scoped>
.card {
  margin: 0 16px;
}
</style>
