<template>
  <div class="form-group">
    <div
      class="w-75 d-flex justify-content-between mb-3 mx-auto"
      v-for="(item, index) in values"
      :key="'val_' + index"
    >
      <input
        type="text"
        class="form-control"
        v-model="item.val"
        @input="updateValue()"
        :placeholder="'Proposition ' + (index+1)"
      />
      <button type="button" class="btn bg-white m-0" @click="removeValue(index)">
        <font-awesome-icon class="text-danger" :icon="['far', 'trash-alt']" />
      </button>
    </div>
    <button
      type="button"
      id="addProposition"
      class="btn btn-primary m-0"
      @click="addValue()"
    >Ajouter une alternative</button>
  </div>
</template>

<script>
export default {
  name: "MultiInput",
  props: {
    childToParent: Function,
    initialValues: Array
  },
  data: function() {
    return {
      values: this.initialValues
    };
  },
  watch: {
    values: function(newValues) {
      this.$emit("childToParent", newValues);
    }
  },
  methods: {
    addValue() {
      this.values.push({});
      this.$emit("input", this.values);
    },
    removeValue(index) {
      if (this.values.length > 1) {
        this.values.splice(index, 1);
        this.$emit("input", this.values);
      }
    },
    updateValue() {
      this.$emit("input", this.values);
    }
  }
};
</script>

