<template>
  <div class="col-10 mx-auto">
    <h4 class="ml-3">{{ titleList }}</h4>
    <div class="d-flex flex-wrap">
      <div class="col-6" v-for="item in list" :key="item._id">
        <VotePreviewCard
          :title="item.title"
          :state="voteState(item)"
          :info="infoVote(item)"
          :link="linkVote(item)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import VotePreviewCard from "@/components/VotePreviewCard";

export default {
  name: "PreviewCardsList",
  components: {
    VotePreviewCard
  },
  props: {
    list: {
      type: Array,
      default: function() {
        return [];
      }
    },
    titleList: String
  },
  methods: {
    voteState: function(item) {
      if (item.status == 0) return "Non publié";
      else if (item.status == 1) return "Ouvert";
      else if (item.status == 2) return "Fermé";
      else if (item.status == 3) return "Archivé";
      return "";
    },
    infoVote: function(item) {
      if (item.status == 0) return "Éditer";
      else if (item.status == 1)
        return (
          item.nbBulletins + " bulletin" + (item.nbBulletins > 1 ? "s" : "")
        );
      return "";
    },
    linkVote: function(item) {
      return (item.status == 0 ? "/edit/" : "/vote/") + item._id;
    }
  }
};
</script>

<style lang="scss" scoped></style>
