import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Vote from "../views/Vote.vue";
import EditVote from "../views/EditVote.vue";
import Account from "../views/Account.vue";
import Settings from "../views/Settings.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
    beforeEnter: (to, from, next) => {
      if (localStorage.getItem("currentUser")) next("/account/" + JSON.parse(localStorage.getItem("currentUser"))._id);
      else next();
    }
  },
  {
    path: "/vote/new",
    name: "CreateAnonymVote",
    component: EditVote
  },
  {
    path: "/vote/:vote_id",
    name: "Vote",
    component: Vote
  },
  {
    path: "/edit/:vote_id",
    name: "EditVote",
    component: EditVote
  },
  {
    path: "/account/:user_id",
    name: "Account",
    component: Account,
    beforeEnter: (to, from, next) => {
      const currentUser = JSON.parse(localStorage.getItem("currentUser"));
      if (to.params.user_id !== currentUser._id)
        next({ name: "Home" });
      else next();
    },
    children: [
      {
        path: ":vote_id",
        component: Vote
      },
      {
        path: "settings",
        component: Settings
      }
    ]
  },
  {
    path: "/about",
    name: "About",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue")
  }
];

const router = new VueRouter({
  routes
});

export default router;
