<template>
  <section class="col-6 mx-auto text-center">
    <h3 class="my-4">{{ !isNew ? "Édition du vote" : "Nouveau vote" }}</h3>

    <form class @submit="openVote">
      <!-- infos générales -->
      <div class="mb-5">
        <h4>Informations générales</h4>
        <div class="form-group">
          <label for="title">Titre</label>
          <input
            id="title"
            type="text"
            class="form-control"
            name="title"
            aria-describedby="emailHelp"
            placeholder="Titre de votre vote"
            required
            v-model="title"
          />
        </div>
        <div class="form-group">
          <label for="question">Question</label>
          <textarea
            id="question"
            type="text"
            class="form-control"
            name="question"
            placeholder="Question non ambiguë"
            required
            v-model="description"
          />
        </div>
      </div>

      <!-- definition des choix -->
      <div class="mb-5" v-if="choices.length > 0">
        <h4>Propositions</h4>
        <div id="propositions">
          <MultiInput v-on:childToParent="updateChoices" :initialValues="choices" />
        </div>
      </div>

      <!-- délai de suppression -->
      <div class="mb-5" v-if="isNew && !userIsLogged">
        <h4>Suppression</h4>
        <label for>Après</label>
        <div class="custom-control custom-radio">
          <input
            type="radio"
            id="one"
            name="limit"
            class="custom-control-input"
            :value="1"
            v-model="pickedDelay"
          />
          <label class="custom-control-label" for="one">1 jour</label>
        </div>
        <div class="custom-control custom-radio">
          <input
            type="radio"
            id="two"
            name="limit"
            class="custom-control-input"
            :value="2"
            v-model="pickedDelay"
          />
          <label class="custom-control-label" for="two">2 jours</label>
        </div>
        <div class="custom-control custom-radio">
          <input
            type="radio"
            id="seven"
            name="limit"
            class="custom-control-input"
            :value="7"
            v-model="pickedDelay"
          />
          <label class="custom-control-label" for="seven">7 jours</label>
        </div>
      </div>

      <button type="button" class="btn btn-outline-dark" @click="cancelModifications">Annuler</button>
      <button
        v-if="!isNew"
        id="saveVote"
        type="button"
        class="btn btn-outline-primary"
        @click="saveVote"
      >Enregistrer</button>
      <button id="publishVote" type="submit" class="btn btn-primary">Publier le vote</button>
    </form>
  </section>
</template>

<script>
import axios from "axios";
import MultiInput from "@/components/MultiInput";

export default {
  name: "EditVote",
  components: {
    MultiInput
  },
  data: function() {
    return {
      API_URL: process.env.VUE_APP_API_URL,
      isNew: this.$route.name !== "EditVote",
      userIsLogged: !!localStorage.getItem('currentUser'),
      currentUser: JSON.parse(localStorage.getItem('currentUser')),
      token: localStorage.getItem('user-token'), 
      vote: {},
      title: "",
      user_id: null,
      description: "",
      status: 1,
      openDate: null,
      nbChoices: 0,
      deleteDate: null,
      pickedDelay: 1,
      choices: [],
      notEnoughChoices: false
    };
  },
  created() {
    if (!this.isNew) {
      axios.defaults.baseURL = this.API_URL;
      axios.defaults.headers.common["Cockpit-Token"] = this.token;
      axios
        .post(`/collections/get/votes`, {
          filter: {
            _id: this.$route.params.vote_id
          }
        })
        .then(response => {
          this.vote = response.data.entries[0];
          if (this.vote.nbChoices > 0) {
            axios
              .post(`/collections/get/choices`, {
                filter: {
                  vote_id: this.$route.params.vote_id
                }
              })
              .then(response => {
                this.choices = response.data.entries.map(choice => {
                  return { val: choice.name };
                });
              });
          } else this.choices = [{ val: "" }, { val: "" }];
          this.title = this.vote.title;
          this.user_id = this.vote.user_id;
          this.description = this.vote.description;
          this.status = this.vote.status;
          this.openDate = this.vote.openDate;
          this.nbChoices = this.vote.nbChoices;
          this.deleteDate = this.vote.deleteDate || null;
        })
        .catch(e => {
          console.error(e);
        });
    }
  },
  methods: {
    updateChoices(val) {
      this.choices = val;
    },
    addDays(date, days) {
      let result = new Date(date);
      result.setDate(result.getDate() + days);
      return result;
    },
    cancelModifications() {
      if (confirm("Êtes-vous sûr de vouloir perdre vos modifications ?"))
        this.$router.push('/account/' + this.currentUser._id)
    },
    getDatas(infos) {
      const data = {
        _id: this.vote._id,
        title: this.title,
        user_id: this.user_id || process.env.VUE_APP_ANONYM,
        description: this.description,
        status: infos.status,
        openDate: this.openDate || new Date(),
        nbChoices: this.choices.reduce((acc, c) => acc + (c.val ? 1 : 0), 0),
        nbBulletins: 0,
        deleteDate:
          this.isNew && !this.userIsLogged
            ? this.addDays(this.openDate, this.pickedDelay)
            : null
      };
      return data;
    },
    saveVote() {
      let datas = this.getDatas({ status: this.status });
      this.saveModifications(datas);
    },
    openVote(e) {
      e.preventDefault();
      const datas = this.getDatas({ status: 1});
      if (datas.nbChoices < 2) return;
      this.saveModifications(datas);
    },
    saveModifications(voteData) {
      axios.defaults.baseURL = this.API_URL;
      axios.defaults.headers.common["Cockpit-Token"] = this.token;
      axios
        .post(`/collections/save/votes`, {
          data: { ...voteData }
        })
        .then(async () => {
          await this.saveChoices(this.vote._id);
          this.$router.push("/");
        })
        .catch(e => {
          console.error(e);
        });
    },
    createChoices(vote_id) {
      for (const choice of this.choices) {
        if (choice.val) {
          axios
            .post(`/collections/save/choices`, {
              data: {
                vote_id: vote_id,
                name: choice.val
              }
            })
            .catch(e => {
              console.error(e);
            });
        }
      }
    },
    async saveChoices(vote_id) {
      return new Promise(resolve => {
        if (this.vote.nbChoices > 0) {
          axios
            .post(`/collections/remove/choices`, {
              filter: { vote_id: vote_id }
            })
            .then(() => {
              this.createChoices(vote_id);
              resolve("done");
            })
            .catch(e => {
              console.error(e);
            });
        } else {
          this.createChoices(vote_id);
          resolve("done");
        }
      });
    }
  }
};
</script>

<style lang="scss"></style>